.section input[id="slide"] {
  display: none;
}

.section .slidewrap {
  margin: 0 auto;
  overflow: hidden;
  max-width: 1200px;
}

.section .slidelist {
  white-space: nowrap;
  font-size: 0;
}
.section .slidelist > li {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  transition: all 0.5s;
}

.section .slidelist > li > a {
  display: block;
  position: relative;
}

.section .slidelist > li > a img {
  display: block;
  margin: auto;
}

#img_container {
  text-align: center;
}

.section .slidelist label {
  position: absolute;
  z-index: 10;
  top: 50%;
  transform: translateY(-50%);
  padding: 50px;
  cursor: pointer;
}

.section .slidelist .left {
  left: 30px;
  background: url("https://www.freepnglogos.com/uploads/instagram-icon-png/black-hd-instagram-icon-simple-black-design-9.png")
    center center / 100% no-repeat;
}

.section .slidelist .right {
  right: 30px;
  background: url("https://www.freepnglogos.com/uploads/instagram-icon-png/black-hd-instagram-icon-simple-black-design-9.png")
    center center / 100% no-repeat;
}

/* 2022-12-07 슬라이드이미지 화면전환*/
.section [id="slide01"]:checked ~ .slidewrap .slidelist > li {
  transform: translate(0%);
}
.section [id="slide02"]:checked ~ .slidewrap .slidelist > li {
  transform: translate(-100%);
}
.section [id="slide03"]:checked ~ .slidewrap .slidelist > li {
  transform: translate(-200%);
}
.section [id="slide04"]:checked ~ .slidewrap .slidelist > li {
  transform: translate(-300%);
}
.section [id="slide05"]:checked ~ .slidewrap .slidelist > li {
  transform: translate(-400%);
}
.section [id="slide06"]:checked ~ .slidewrap .slidelist > li {
  transform: translate(-500%);
}
.section [id="slide07"]:checked ~ .slidewrap .slidelist > li {
  transform: translate(-500%);
}
